const validationSchema = yup.object({
  amount: yup.number().min(1).required(),
});

function FormItem(props: { number: number }) {
  const formikItem = useFormik({
    initialValues: {
      category: `10`,
      item: `20`,
      amount: 1,
    },
    validationSchema: validationSchema,
    onSubmit: () => {},
  });

  const { number } = props;
  return (
    <form onSubmit={formikItem.handleSubmit}>
      <Box display={'flex'} alignItems={'center'}>
        <Typography>獎勵{number}</Typography>
        <Box sx={{ m: 1, minWidth: 120 }}>
          <FormControl fullWidth size="small">
            <InputLabel id="demo-simple-select-label">物品種類</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              label="物品種類"
              value={formikItem.values.category}
              onChange={formikItem.handleChange}
            >
              <MenuItem value={`10`} selected>
                Ten
              </MenuItem>
            </Select>
          </FormControl>
        </Box>
        <Box sx={{ m: 1, minWidth: 120 }}>
          <FormControl fullWidth size="small">
            <InputLabel id="demo-simple-select-label">物品</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              label="物品 ID"
              value={formikItem.values.item}
              onChange={formikItem.handleChange}
            >
              <MenuItem value={`20`} selected>
                Twenty
              </MenuItem>
            </Select>
          </FormControl>
        </Box>
        <Box sx={{ m: 1, maxWidth: 120 }}>
          <FormControl>
            <TextField
              id="amount"
              name="amount"
              size="small"
              label="數量"
              type="number"
              value={formikItem.values.amount}
              onChange={formikItem.handleChange}
              error={Boolean(formikItem.errors.amount)}
            />
          </FormControl>
        </Box>
      </Box>
    </form>
  );
}

const Transition = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>
) {
  return <Slide direction="up" ref={ref} {...props} />;
});

