interface TablePaginationActionsProps {
  count: number;
  page: number;
  rowsPerPage: number;
  onPageChange: (
    event: React.MouseEvent<HTMLButtonElement>,
    newPage: number
  ) => void;
}

function TablePaginationActions(props: TablePaginationActionsProps) {
  const theme = useTheme();
  const { count, page, rowsPerPage, onPageChange } = props;

  const handleFirstPageButtonClick = (
    event: React.MouseEvent<HTMLButtonElement>
  ) => {
    onPageChange(event, 0);
  };

  const handleBackButtonClick = (
    event: React.MouseEvent<HTMLButtonElement>
  ) => {
    onPageChange(event, page - 1);
  };

  const handleNextButtonClick = (
    event: React.MouseEvent<HTMLButtonElement>
  ) => {
    onPageChange(event, page + 1);
  };

  const handleLastPageButtonClick = (
    event: React.MouseEvent<HTMLButtonElement>
  ) => {
    onPageChange(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
  };

  return (
    <Box sx={{ flexShrink: 0, ml: 2.5 }}>
      <IconButton
        onClick={handleFirstPageButtonClick}
        disabled={page === 0}
        aria-label="first page"
      >
        {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
      </IconButton>
      <IconButton
        onClick={handleBackButtonClick}
        disabled={page === 0}
        aria-label="previous page"
      >
        {theme.direction === 'rtl' ? (
          <KeyboardArrowRight />
        ) : (
          <KeyboardArrowLeft />
        )}
      </IconButton>
      <IconButton
        onClick={handleNextButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="next page"
      >
        {theme.direction === 'rtl' ? (
          <KeyboardArrowLeft />
        ) : (
          <KeyboardArrowRight />
        )}
      </IconButton>
      <IconButton
        onClick={handleLastPageButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="last page"
      >
        {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
      </IconButton>
    </Box>
  );
}

interface IMailTableProps {
  data: any;
  onClickRefresh: () => void;
  onClickDeleteMail: (mailId: number) => void;
  onClickTerminateMail: (mailId: number) => void;
  onClickModifyMail: (mailId: number) => void;
}

function Tables(props: IMailTableProps) {
  const { data } = props;
  const {
    onClickRefresh,
    onClickDeleteMail,
    onClickModifyMail,
    onClickTerminateMail,
  } = props;
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);

  // Avoid a layout jump when reaching the last page with empty rows.
  const emptyRows =
    page > 0 ? Math.max(0, (1 + page) * rowsPerPage - data.result.length) : 0;

  const handleChangePage = (
    event: React.MouseEvent<HTMLButtonElement> | null,
    newPage: number
  ) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  if (data.result !== undefined) {
    return (
      <TableContainer component={Paper}>
        <Table aria-label="collapsible table">
          <TableHead>
            <TableRow>
              <TableCell />
              <TableCell component="th" scope="row">
                信件 ID
              </TableCell>
              <TableCell align="right">平台</TableCell>
              <TableCell align="right">發送對象</TableCell>
              <TableCell align="right">備註</TableCell>
              <TableCell align="right">開始時間</TableCell>
              <TableCell align="right">過期時間</TableCell>
              <TableCell align="right">狀態</TableCell>
              <TableCell align="right"></TableCell>
              <TableCell align="right"></TableCell>
              <TableCell align="right">
                <IconButton onClick={() => onClickRefresh()}>
                  <RefreshIcon></RefreshIcon>
                </IconButton>
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {(rowsPerPage > 0
              ? data.result.slice(
                  page * rowsPerPage,
                  page * rowsPerPage + rowsPerPage
                )
              : data.result
            ).map((row: any) => (
              <Row
                key={row.id}
                row={row}
                onClickModifyMail={() => onClickModifyMail(row.id)}
                onClickTerminateMail={() => onClickTerminateMail(row.id)}
                onClickDeleteMail={() => onClickDeleteMail(row.id)}
              />
            ))}
            {emptyRows > 0 && (
              <TableRow style={{ height: 53 * emptyRows }}>
                <TableCell colSpan={6} />
              </TableRow>
            )}
            {data.result.length === 0 && (
              <Typography variant="h4">這裡沒有任何資料</Typography>
            )}
          </TableBody>
          <TableFooter>
            <TableRow>
              <TablePagination
                rowsPerPageOptions={[5, 10, 15, { label: 'All', value: -1 }]}
                count={data.result.length}
                rowsPerPage={rowsPerPage}
                page={page}
                SelectProps={{
                  inputProps: {
                    'aria-label': 'rows per page',
                  },
                  native: true,
                }}
                onPageChange={handleChangePage}
                onRowsPerPageChange={handleChangeRowsPerPage}
                ActionsComponent={TablePaginationActions}
              />
            </TableRow>
          </TableFooter>
        </Table>
      </TableContainer>
    );
  } else {
    return <></>;
  }
}

interface IMailState {
  title: string;
  editable: boolean;
  deletable: boolean;
  terminatable: boolean;
}

function addDays(date: Date, days: number): Date {
  var result = new Date(date);
  result.setDate(result.getDate() + days);
  return result;
}

function getMailState(
  isLimit: boolean,
  startTime: number,
  endTime: number
): IMailState {
  let result: IMailState = {
    title: '',
    editable: false,
    deletable: false,
    terminatable: false,
  };
  let now = new Date();
  let startDateTime = new Date(startTime * 1000);
  let endDateTime = new Date(endTime * 1000);
  let expireDateTime = addDays(endDateTime, 30);

  if (isLimit) {
    result.title = '中止';
    result.editable = true;
    result.deletable = true;
    if (now <= startDateTime) {
      result.terminatable = true;
    }
  } else if (now <= startDateTime) {
    result.title = '未發送';
    result.editable = true;
    result.deletable = true;
    result.terminatable = true;
  } else if (startDateTime <= now && now < endDateTime) {
    result.title = '已發送';
  } else if (endDateTime <= now && now < expireDateTime) {
    result.title = '已過期';
  } else if (expireDateTime < now) {
    result.title = '可刪除';
    result.deletable = true;
  }
  return result;
}

interface IMailRowProps {
  row: any;
  onClickDeleteMail: () => void;
  onClickTerminateMail: () => void;
  onClickModifyMail: () => void;
}

function Row(props: IMailRowProps) {
  const row = props.row;
  const { onClickDeleteMail, onClickModifyMail, onClickTerminateMail } = props;
  const state: IMailState = getMailState(
    row.is_limit,
    row.start_time,
    row.end_time
  );
  const [open, setOpen] = React.useState(false);

  return (
    <React.Fragment>
      <TableRow sx={{ '& > *': { borderBottom: 'unset' } }}>
        <TableCell>
          {row.reward_list.length > 0 && (
            <IconButton
              aria-label="expand row"
              size="small"
              onClick={() => setOpen(!open)}
            >
              {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
            </IconButton>
          )}
        </TableCell>
        <TableCell component="th" scope="row">
          {row.id}
        </TableCell>
        <TableCell align="right">{row.platform}</TableCell>
        <TableCell align="right">
          {row.is_all_player ? '全部' : '指定'}
        </TableCell>
        <TableCell align="right">Default Description</TableCell>
        <TableCell align="right">
          <Typography variant="body2">
            {timestampToDate(row.start_time)}
          </Typography>
        </TableCell>
        <TableCell align="right">
          <Typography variant="body2">
            {timestampToDate(row.end_time)}
          </Typography>
        </TableCell>
        <TableCell align="right">{state.title}</TableCell>
        <TableCell align="right">
          <Button
            variant="outlined"
            disabled={!state.editable}
            onClick={() => onClickModifyMail()}
          >
            查看/編輯
          </Button>
        </TableCell>
        <TableCell align="right">
          <Button
            variant="outlined"
            disabled={!state.terminatable}
            onClick={() => onClickTerminateMail()}
          >
            {row.is_limit ? '取消中止' : '中止'}
          </Button>
        </TableCell>
        <TableCell align="right">
          <Button
            variant="contained"
            disabled={!state.deletable}
            onClick={() => onClickDeleteMail()}
          >
            刪除
          </Button>
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box sx={{ margin: 1 }}>
              <Table size="small">
                <TableHead>
                  <TableRow>
                    <TableCell>RewardID</TableCell>
                    <TableCell align="right">ObjectType</TableCell>
                    <TableCell align="right">Quantity</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {row.reward_list.map((reward: any, index: number) => (
                    <TableRow key={index}>
                      <TableCell>{reward.object.item_id}</TableCell>
                      <TableCell align="right">{reward.object_type}</TableCell>
                      <TableCell align="right">{reward.object.count}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}

