import { useCallback, useState, useEffect } from 'react';
import { httpConfig } from './factory';
import axios from 'axios';
import giftData from '../asset/data/get_gift_list.json'
import mailData from '../asset/data/get_mail_list.json'

axios.defaults.baseURL = 'http://localhost/project_ls/api/v1/admin/';

// const operations = {
//   setEndTime: 'http://localhost/',
//   addCodes: 'http://localhost',
//   createCode: 'http://localhost',
// };

function useFetchGiftCodeData() {
  const [response, setResponse] = useState(undefined);
  const [refresh, setRefresh] = useState(true);

  const fetchData = useCallback(() => {
    setResponse(giftData);
  }, []);

  useEffect(() => {
    if (refresh) {
      setResponse(undefined);
      setTimeout(() => fetchData(), 1000);
      setRefresh(false);
    }
  }, [refresh, fetchData]);
  return { data: response, refreshData: () => setRefresh(true) };
}


function useFetchMailData() {
  const [response, setResponse] = useState(undefined);
  const [refresh, setRefresh] = useState(true);

  const fetchData = useCallback(() => {
    setResponse(mailData);
  }, []);

  useEffect(() => {
    if (refresh) {
      setResponse(undefined);
      setTimeout(() => fetchData(), 1000);
      setRefresh(false);
    }
  }, [refresh, fetchData]);
  return { data: response, refreshData: () => setRefresh(true) };
}


function useHttpRequest() {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);

  const sendRequest = useCallback(async (config: httpConfig, setData: any) => {
    setIsLoading(true);
    setError(null);
    let response;
    try {
      response = await axios({
        url: config.url,
        method: config.method ? config.method : 'GET',
        headers: config.headers ? config.headers : {},
        data: config.data ? config.data : null
      });
    } catch (error: any) {
      setError(error.message || 'Something went wrong!');
    } finally {
      if (response) {
        setData(response.data);
      }
      setIsLoading(false);
    }
  }, []);

  return { isLoading, error, sendRequest };
}

export { useFetchGiftCodeData, useFetchMailData, useHttpRequest };
