import { object } from 'yup';

export interface httpConfig {
  url: string;
  method: string;
  headers: {
    'Content-Type': string;
  };
  data: object;
}

function createHttpConfig(
  url: string,
  method: string = 'GET',
  data: object = {}
): httpConfig {
  let result: httpConfig = {
    url: url,
    method: method,
    headers: { 'Content-Type': 'application/json' },
    data: object,
  };
  return result;
}

export { createHttpConfig };
