import * as React from 'react';
import { Routes, Route, useNavigate } from 'react-router-dom';
import { getAuthToken, delAuthToken } from '../util/auth';
import { useHttpRequest } from '../util/hook';
import { httpConfig, createHttpConfig } from '../util/factory';
import Home from './home';
import GiftCode from './giftcode';
import Mail from './mail';
import Error from './error';
import {
  Box,
  Drawer,
  CssBaseline,
  ListItemIcon,
  SvgIcon,
  AppBar,
  Toolbar,
  List,
  Divider,
  ListItem,
  ListItemButton,
  ListItemText,
  Link,
  Grid,
  Button,
  Chip,
} from '@mui/material';

import GroupWork from '@mui/icons-material/GroupWork';
import CardGiftcardIcon from '@mui/icons-material/CardGiftcard';
import EmailIcon from '@mui/icons-material/Email';
import FormatListBulletedIcon from '@mui/icons-material/FormatListBulleted';
import ScienceIcon from '@mui/icons-material/Science';

const drawerWidth = 240;
const companyName = 'Icecone';

const operations = [
  {
    id: 1,
    route: 'gift',
    name: '兌換碼',
    icon: CardGiftcardIcon,
    isAvailable: true,
  },
  { id: 2, route: 'mail', name: '信箱', icon: EmailIcon, isAvailable: true },
  {
    id: 3,
    route: 'bulletin',
    name: '公告',
    icon: FormatListBulletedIcon,
    isAvailable: false,
  },
  {
    id: 4,
    route: 'testing',
    name: '測試',
    icon: ScienceIcon,
    isAvailable: false,
  },
];

export default function Body() {
  const { isLoading, error, sendRequest: fetch } = useHttpRequest();
  const navigate = useNavigate();
  const isActive = React.useRef(false);
  // const [isReactivate, setReactivate] = React.useState(false);
  // const [isDownloading, DownloadError, sendDownloadRequest] = useHttpRequest();
  // const [isReset, ResetError, sendResetDBRequest] = useHttpRequest();

  React.useEffect(() => {
    console.count("Body Render Times");
    let token = getAuthToken();
    if (token !== 'access_token') {
      navigate('/login');
    }
    
    let config = createHttpConfig('/heartbeat');
    fetch(config, (res: Response) => {
      if (res.status === 200) {
        isActive.current = true;
      } else {
        alert(error);
      }
    });
  }, [error, fetch]);

  const handleLogout = () => {
    delAuthToken();
    navigate('/login');
  };

  const handleActivate = () => {
    // if (isActive.current === false) {
    //   setReactivate((prevState) => !prevState);
    // }
  };

  return (
    <div className="body">
      <Box sx={{ display: 'flex' }}>
        <CssBaseline />
        <AppBar
          position="fixed"
          sx={{
            width: `calc(100% - ${drawerWidth}px)`,
            ml: `${drawerWidth}px`,
          }}
        >
          <Toolbar>
            <Grid container display="flex" justifyContent="right">
              <Button color="inherit" onClick={handleLogout}>
                登出
              </Button>
            </Grid>
          </Toolbar>
        </AppBar>
        <Drawer
          sx={{
            width: drawerWidth,
            flexShrink: 0,
            '& .MuiDrawer-paper': {
              width: drawerWidth,
              boxSizing: 'border-box',
            },
          }}
          variant="permanent"
          anchor="left"
        >
          <Toolbar>
            <GroupWork sx={{ display: { xs: 'none', md: 'flex' }, mr: 1 }} />
            <Link href="/" variant="h5" underline="none">
              {companyName}
            </Link>
            <Chip
              sx={{ marginLeft: 2 }}
              onClick={() => handleActivate()}
              label={isActive.current ? 'active' : 'inactive'}
              color={isActive.current ? 'success' : 'error'}
            />
          </Toolbar>
          <Divider />
          <List>
            {operations.map((operation) => (
              <ListItem key={operation.id} disablePadding>
                <ListItemButton
                  href={operation.route}
                  disabled={!operation.isAvailable}
                  disableRipple
                >
                  <ListItemIcon>
                    <SvgIcon component={operation.icon} />
                  </ListItemIcon>
                  <ListItemText primary={operation.name} />
                </ListItemButton>
              </ListItem>
            ))}
          </List>
          <Divider />
          <Button
            sx={{ marginLeft: '20%', width: '60%', height: '4%', marginTop: 1 }}
            variant="contained"
          >
            下載所有 tsv 檔
          </Button>
          <Button
            sx={{
              marginLeft: '20%',
              width: '60%',
              height: '4%',
              marginTop: 1,
              marginBottom: 1,
            }}
            variant="contained"
          >
            重置資料庫
          </Button>
          <Divider />
        </Drawer>
        <Box component="main" sx={{ flexGrow: 1, p: 3 }}>
          <Toolbar />
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/gift" element={<GiftCode />} />
            <Route path="/mail" element={<Mail />} />
            <Route path="*" element={<Error />} />
            {/*<Route path="/bulletin" element={<Bulletin />} />
                <Route path="/testing" element={<Testing />} /> */}
          </Routes>
        </Box>
      </Box>
    </div>
  );
}
